.switch-container {
  margin: 0 0.6em;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.react-switch-checkbox {
  height: 0;
  width: 0;
  visibility: hidden;
  display: none;
}

.react-switch-label {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 40px;
  height: 21px;
  background: grey;
  border-radius: 40px;
  position: relative;
  transition: background-color 0.2s;
  margin-right: 0.5em;
}

.react-switch-label .react-switch-button {
  content: '';
  position: absolute;
  top: 2.5px;
  left: 2.5px;
  width: 16px;
  height: 16px;
  border-radius: 16px;
  transition: 0.2s;
  background: #fff;
  box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29);
}

.react-switch-checkbox:checked + .react-switch-label .react-switch-button {
  left: calc(100% - 2.5px);
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.react-switch-label:active .react-switch-button {
  width: 20px;
}

